.btn, .btn-red {
  border: 1px solid #fc6001;
  position: relative;
  text-align: center;
  border-radius: 10px;
  padding: 0 5px;
}

.btn span, .btn-red span {
  position: relative;
  z-index: 1;
  transition: all 0.2s ease;
  color: #fff;
}

.btn:after, .btn-red:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  background: #fff;
  opacity: 0;
  transition: all 0.2s ease;
}

.btn:hover:after, .btn-red:hover:after {
  opacity: 1;
}

.btn:hover span, .btn-red:hover span {
  color: #fc6001;
}

